function LanguageSwitcher() {
    const { i18n } = useTranslation();
    const [isOpen, setIsOpen] = useState(false);
    const changeLanguage = (lng: string) => { i18n.changeLanguage(lng); setIsOpen(false); };
    const languages = [{ code: 'en', flag: '' }, { code: 'es', flag: '' }];
    const current = languages.find(l => l.code === i18n.language) || languages[0];
    return (
      <div className="relative z-50">
        <button onClick={() => setIsOpen(!isOpen)} className="p-3 bg-white/50 backdrop-blur-md rounded-full hover:bg-white shadow-sm transition-all flex items-center gap-2 border border-zinc-100">
          <Globe size={18} className="text-zinc-800"/> <span className="text-xs font-black hidden md:block">{current.flag}</span>
        </button>
        {isOpen && (
           <>
           <div className="fixed inset-0 z-40" onClick={() => setIsOpen(false)}></div>
           <div className="absolute top-full right-0 mt-2 bg-white rounded-[20px] shadow-2xl border border-zinc-100 py-3 min-w-[120px] z-50 overflow-hidden animate-in fade-in zoom-in-95">
            {languages.map((lang) => (
              <button key={lang.code} onClick={() => changeLanguage(lang.code)} className="w-full px-6 py-3 text-left text-[10px] font-black uppercase hover:bg-zinc-50 flex items-center gap-3 transition-colors">
                <span className="text-base">{lang.flag}</span> {lang.code.toUpperCase()}
              </button>
            ))}
          </div>
          </>
        )}
      </div>
    );
}

function CustomToast({ message, type, onClose }: any) {
  useEffect(() => { const t = setTimeout(onClose, 4000); return () => clearTimeout(t); }, [onClose]);
  if (!message) return null;
  return (
    <div className={`fixed top-8 left-1/2 -translate-x-1/2 z-[10000] flex items-center gap-3 px-8 py-4 rounded-full shadow-2xl animate-in slide-in-from-top-6 text-white font-black text-[10px] uppercase tracking-widest ${type === 'error' ? 'bg-red-600' : 'bg-emerald-600'}`}>
      {type === "error" ? <AlertCircle size={18} /> : <CheckCircle size={18} />} {String(message)}
    </div>
  );
}

// --- CHAT SYSTEM ---
function ChatSystem({ orderId, isAdmin = false, user }: any) {
  const [messages, setMessages] = useState<any[]>([]);
  const [text, setText] = useState("");
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!user || !db) return;
    const q = collection(db, 'artifacts', appId, 'public', 'data', 'messages');
    return onSnapshot(q, (snap) => {
      const filtered = snap.docs.map(d => ({id: d.id, ...d.data()} as any))
        .filter(m => m.orderId === orderId)
        .sort((a,b) => new Date(a.time).getTime() - new Date(b.time).getTime());
      setMessages(filtered);
    });
  }, [orderId, user]);

  useEffect(() => { scrollRef.current?.scrollIntoView({ behavior: 'smooth' }); }, [messages]);

  const send = async (e: any) => {
    e.preventDefault();
    if (!text.trim() || !user) return;
    await addDoc(collection(db, 'artifacts', appId, 'public', 'data', 'messages'), {
      orderId, text, sender: isAdmin ? 'admin' : 'client', time: new Date().toISOString()
    });
    setText("");
  };

  return (
    <div className="bg-zinc-50 rounded-[45px] border border-zinc-100 overflow-hidden flex flex-col h-[300px]">
      <div className="p-5 bg-white border-b flex items-center gap-2 font-black text-[9px] uppercase tracking-widest text-zinc-400">Support Chat</div>
      <div className="flex-1 overflow-y-auto p-5 space-y-4 text-zinc-950">
        {messages.map(m => (
          <div key={m.id} className={`flex ${m.sender === (isAdmin ? 'admin' : 'client') ? 'justify-end' : 'justify-start'}`}>
            <div className={`max-w-[85%] p-4 rounded-[25px] text-xs font-bold leading-relaxed shadow-sm ${m.sender === (isAdmin ? 'admin' : 'client') ? 'bg-zinc-950 text-white shadow-xl' : 'bg-white border text-zinc-600 shadow-sm'}`}>{m.text}</div>
          </div>
        ))}
        <div ref={scrollRef} />
      </div>
      <form onSubmit={send} className="p-4 bg-white border-t flex gap-3">
        <input value={text} onChange={e => setText(e.target.value)} placeholder="..." className="flex-1 px-6 py-3 bg-zinc-50 rounded-2xl text-xs outline-none font-bold text-zinc-950" />
        <button type="submit" className="p-3 bg-zinc-950 text-white rounded-2xl active:scale-90 transition-transform"><Send size={18}/></button>
      </form>
    </div>
  );
}

