// --- FORMULARIO DE TARJETA STRIPE REAL (SEPARADO Y SEGURO) ---
function StripeCardForm({ onSuccess, onError, amount }: any) {
  const stripe = useStripe();
  const elements = useElements();
  
  const [errorMsg, setErrorMsg] = useState("");
  const [paymentRequest, setPaymentRequest] = useState<any>(null);

  // --- LOGICA DE APPLE PAY / GOOGLE PAY SEGURA ---
  useEffect(() => {
    if (stripe) {
      const pr = stripe.paymentRequest({
        country: 'US',
        currency: 'usd',
        total: {
          label: 'Total',
          amount: Math.round(parseFloat(amount) * 100),
        },
        requestPayerName: true,
        requestPayerEmail: true,
      });

      pr.canMakePayment().then(result => {
        if (result) {
          setPaymentRequest(pr);
        }
      });

      // KEY FIX: Verify event status before success
      pr.on('paymentmethod', async (ev) => {
        if (ev.paymentMethod) {
            onSuccess(ev.paymentMethod);
            ev.complete('success'); 
        } else {
            ev.complete('fail');
            onError("Payment not verified by device");
        }
      });
    }
  }, [stripe, amount, onSuccess, onError]);

  const handleSubmit = async (event: any) => {
    event.preventDefault();
    if (!stripe || !elements) return;

    setLoading(true);
    setErrorMsg("");
    
    const cardElement = elements.getElement(CardElement);
    const { error, paymentMethod } = await stripe.createPaymentMethod({
      type: 'card',
      card: cardElement!,
    });

    if (error) {
      console.log("[Stripe Error]", error);
      setErrorMsg(error.message || "Card declined");
      onError(error.message || "Card declined");
      setLoading(false);
    } else {
      console.log("[Stripe Success]", paymentMethod);
      onSuccess(paymentMethod);
      setLoading(false);
    }
  };

  return (
    <div className="space-y-8">
      {/* SEPARACIN CLARA PARA APPLE/GOOGLE PAY */}
      {paymentRequest && (
        <div className="bg-zinc-50 border border-zinc-200 rounded-3xl p-5 text-center">
           <p className="text-[10px] font-black uppercase text-zinc-400 mb-4 tracking-widest">Express Checkout</p>
           <div className="h-[48px] w-full">
             <PaymentRequestButtonElement options={{paymentRequest}} />
           </div>
           <div className="relative mt-8 mb-2">
              <div className="absolute inset-0 flex items-center"><div className="w-full border-t border-zinc-200"></div></div>
              <span className="relative bg-zinc-50 px-4 text-[9px] font-black uppercase text-zinc-400">OR PAY WITH CARD</span>
           </div>
        </div>
      )}

      <form onSubmit={handleSubmit} className="space-y-6">
        <div className={`p-5 bg-white border rounded-2xl transition-colors shadow-sm w-full overflow-hidden ${errorMsg ? 'border-red-500 bg-red-50' : 'border-zinc-200'}`}>
          <CardElement options={{
            style: {
              base: {
                fontSize: '15px', 
                color: '#1a1a1a',
                fontSmoothing: 'antialiased',
                '::placeholder': { color: '#aab7c4' },
                iconColor: '#10b981',
              },
              invalid: { color: '#ef4444', iconColor: '#ef4444' },
            },
            hidePostalCode: true,
          }}/>
        </div>
        
        {errorMsg && (
          <div className="flex items-center gap-2 text-red-500 bg-red-100 p-3 rounded-xl animate-pulse">
             <AlertCircle size={16}/>
             <span className="text-xs font-bold uppercase">{errorMsg}</span>
        </div>
        )}

        <button type="submit" disabled={!stripe || loading} className="w-full py-6 bg-zinc-950 text-white rounded-full font-black uppercase text-xs tracking-widest hover:bg-emerald-600 transition-colors shadow-xl flex items-center justify-center gap-3">
          {loading ? <><div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div> Processing...</> : `Pay $${amount} Now`}
        </button>
      </form>
    </div>
  );
}

