export default function App() {
  const { t, i18n } = useTranslation();
  const [user, setUser] = useState<any>(null);
  const [view, setView] = useState("home");
  const [products, setProducts] = useState<any[]>([]);
  const [cart, setCart] = useState<any[]>([]);
  const [members, setMembers] = useState<any[]>([]);
  
  // CONFIGURACIN INICIAL
  const [config, setConfig] = useState<any>({ 
    brandName: "STUDIO ELITE", 
    brandSubtitle: "Contemporary Curation",
    heroImage: "https://images.unsplash.com/photo-1490481651871-ab68de25d43d?auto=format&fit=crop&w=1600&q=80",
    customIcon: "",
    taxPercent: 7,
    membershipCost: 20,
    membershipDiscount: 20,
    savingsPeriod: 3, 
    visitsPerMonth: 2, 
    promoText: "Join the Club", 
    stripePublicKey: "pk_test_TYooMQauvdEDq54NiTphI7jx", 
    stripeSecretKey: "", 
    zelleInfo: "",
    zelleInstructions: "Please transfer the total amount to the Zelle info provided. Include your Order # in the memo field.",
    adminUsername: "admin",
    adminPassword: "123",
    adminPhone: "", 
    recoveryPin: "0000"
  });
  
  const [activeCategory, setActiveCategory] = useState("Todos");
  const [stripePromise, setStripePromise] = useState<any>(null);

  // LOGIC STATES
  const [isMember, setIsMember] = useState(false);
  const [memberData, setMemberData] = useState<any>(null);
  const [showJoinModal, setShowJoinModal] = useState(false);
  const [showCelebration, setShowCelebration] = useState(false);
  const [showUpsellModal, setShowUpsellModal] = useState(false);
  const [showSecondChance, setShowSecondChance] = useState(false);
  const [showMemberMenu, setShowMemberMenu] = useState(false);
  const [lastOrder, setLastOrder] = useState<any>(null);
  
  // Checkout States
  const [checkoutStep, setCheckoutStep] = useState(1); 
  const [selectedMethod, setSelectedMethod] = useState("");
  
  const [form, setForm] = useState({ name: "", phone: "", address: "" });
  const [toast, setToast] = useState<any>({ message: null, type: "success" });

  const showAlert = (msg: string, type = "success") => setToast({ message: String(msg), type });

  useEffect(() => { signInAnonymously(auth); onAuthStateChanged(auth, setUser); }, []);

  // CARGAR CONFIG Y STRIPE
  useEffect(() => {
    if (config.stripePublicKey && !stripePromise) {
      setStripePromise(loadStripe(config.stripePublicKey));
    }

    if (!db) return;
    onSnapshot(collection(db, 'artifacts', appId, 'public', 'data', 'products'), s => setProducts(s.docs.map(d => ({id: d.id, ...d.data()}))));
    onSnapshot(collection(db, 'artifacts', appId, 'public', 'data', 'members'), s => setMembers(s.docs.map(d => ({id: d.id, ...d.data()}))));
    
    onSnapshot(doc(db, 'artifacts', appId, 'public', 'data', 'settings', 'general'), s => {
      if (s.exists()) {
        const data = s.data();
        setConfig((prev: any) => ({...prev, ...data, stripePublicKey: data.stripePublicKey || prev.stripePublicKey}));
      }
    });
  }, [config.stripePublicKey]); 

  // --- VERIFICACIN DE MIEMBRO REAL ---
  useEffect(() => {
    if (form.phone && members.length > 0) {
      const found = members.find((m: any) => m.phone === form.phone);
      if (found) { setIsMember(true); setMemberData(found); }
      else { setIsMember(false); setMemberData(null); }
    }
  }, [form.phone, members]);

  const totals = useMemo(() => {
    const subtotal = cart.reduce((acc, item) => acc + (Number(item.price) * item.qty), 0);
    const discount = isMember ? subtotal * ((config.membershipDiscount || 0) / 100) : 0;
    const tax = (subtotal - discount) * ((config.taxPercent || 0) / 100);
    return { subtotal, discount, tax, total: subtotal - discount + tax };
  }, [cart, isMember, config]);

  const projectedSavings = useMemo(() => {
    const currentPotentialSave = cart.reduce((acc, item) => acc + (Number(item.price) * item.qty), 0) * ((config.membershipDiscount || 20) / 100);
    const totalFuture = currentPotentialSave * (config.visitsPerMonth || 2) * (config.savingsPeriod || 3);
    return { current: currentPotentialSave, future: totalFuture };
  }, [cart, config]);

  const addToCart = (p: any, s: string, c: string, q: number, isB: boolean) => {
    setCart([...cart, { ...p, size: s, color: c, qty: q, isBackorder: isB, key: Math.random() }]);
    setToast({ message: "Added to Bag", type: "success" });
  };

  const removeFromCart = (key: any) => setCart(prev => prev.filter(i => i.key !== key));

  const handleJoin = async () => {
    if (!form.name || !form.phone) { setToast({message: "Name & Phone Required", type: "error"}); return; }
    await addDoc(collection(db, 'artifacts', appId, 'public', 'data', 'members'), {
      name: form.name, phone: form.phone, joinedAt: new Date().toISOString()
    });
    setIsMember(true);
    setShowJoinModal(false);
    setShowUpsellModal(false);
    setShowSecondChance(false);
    setShowCelebration(true);
    if(view === 'cart') setCheckoutStep(4);
  };

  const handleUnsubscribe = async () => {
    if (window.confirm("Sure you want to cancel?")) {
      if (memberData) {
        await addDoc(collection(db, 'artifacts', appId, 'public', 'data', 'ex_members'), memberData);
        await deleteDoc(doc(db, 'artifacts', appId, 'public', 'data', 'members', memberData.id));
        setForm({...form, phone: ""}); 
        setIsMember(false);
        setToast({ message: "Membership Cancelled", type: "error" });
      }
    }
  };

  const handleMethodSelect = (method: string) => {
    setSelectedMethod(method);
    if (!isMember) {
      setShowUpsellModal(true);
    } else {
      setCheckoutStep(4);
    }
  };

  const finalizeOrder = async (paymentDetails: any = null) => {
    const orderNum = Math.random().toString(36).substring(2, 8).toUpperCase();
    const newOrder = {
      orderNumber: orderNum, 
      customer: form, 
      items: cart, 
      totals, 
      status: "Pagado", 
      method: selectedMethod, 
      paymentId: paymentDetails?.id || "manual",
      createdAt: new Date().toISOString()
    };

    await addDoc(collection(db, 'artifacts', appId, 'public', 'data', 'orders'), newOrder);
    
    // GUARDAR ORDEN Y LIMPIAR
    setLastOrder(newOrder); 
    setCart([]); 
    setCheckoutStep(1); 
    setForm({ name: "", phone: "", address: "" });
    setView("order_success"); 
    setToast({ message: "ORDER APPROVED! " + orderNum, type: "success" });
  };
  return (
    <div className={`min-h-screen bg-[#fafafa] text-zinc-900 font-sans ${i18n.language==='hi'?'font-hindi':''}`}>
      <CustomToast message={toast.message} type={toast.type} onClose={() => setToast({...toast, message: null})} />
      
      {showCelebration && (
        <div className="fixed inset-0 z-[3000] bg-black/90 flex items-center justify-center p-6 animate-in zoom-in">
           <div className="bg-white p-12 rounded-[60px] text-center max-w-lg relative overflow-hidden">
              <div className="absolute inset-0 bg-gradient-to-tr from-yellow-100 to-emerald-100 opacity-50"/>
              <div className="relative z-10">
                <Crown size={80} className="mx-auto text-yellow-500 mb-6 animate-bounce" />
                <h2 className="text-5xl font-black italic uppercase mb-4">{tr(i18n.language, "Welcome!", "Felicidades!")}</h2>
                <p className="text-zinc-500 font-bold uppercase text-xs tracking-widest mb-8">
                   {tr(i18n.language, `You saved $${projectedSavings.current.toFixed(2)} on this order!`, `Ahorraste $${projectedSavings.current.toFixed(2)} hoy!`)}
                </p>
                <button onClick={() => setShowCelebration(false)} className="px-10 py-4 bg-zinc-950 text-white rounded-full font-black uppercase text-xs">Continue</button>
              </div>
           </div>
        </div>
      )}

      {showUpsellModal && (
        <div className="fixed inset-0 z-[2500] bg-black/80 backdrop-blur-md flex items-center justify-center p-6">
           <div className="bg-white p-10 rounded-[50px] max-w-md w-full text-center shadow-2xl animate-in slide-in-from-bottom-10">
              <Zap size={60} className="mx-auto text-yellow-500 mb-6" />
              <h3 className="text-4xl font-black italic uppercase mb-4">{tr(i18n.language, "Wait!", "Espera!")}</h3>
              <p className="text-zinc-500 font-bold text-xs uppercase tracking-widest mb-6 leading-relaxed">
                {tr(i18n.language, "Don't pay full price.", "No pagues precio completo.")}<br/>
                {tr(i18n.language, `Save $${projectedSavings.current.toFixed(2)} now.`, `Ahorra $${projectedSavings.current.toFixed(2)} ya.`)}
              </p>
              <div className="p-6 bg-zinc-50 rounded-3xl mb-8 border border-zinc-100">
                 <div className="flex justify-between mb-2"><span className="text-[10px] font-black uppercase text-zinc-400">Regular</span><span className="font-bold text-zinc-400 line-through">${totals.subtotal.toFixed(2)}</span></div>
                 <div className="flex justify-between"><span className="text-[10px] font-black uppercase text-emerald-600">VIP Price</span><span className="font-black text-emerald-600 text-xl">${(totals.subtotal - projectedSavings.current).toFixed(2)}</span></div>
              </div>
              <button onClick={handleJoin} className="w-full py-5 bg-emerald-500 text-white rounded-2xl font-black uppercase text-xs mb-3 shadow-xl">{tr(i18n.language, "Become VIP & Save", "Hacerme VIP y Ahorrar")}</button>
              <button onClick={() => { setShowUpsellModal(false); setShowSecondChance(true); }} className="text-[10px] font-black uppercase text-zinc-400 hover:text-zinc-950">{tr(i18n.language, "No, pay full price", "No, pagar precio full")}</button>
           </div>
        </div>
      )}

      {showSecondChance && (
        <div className="fixed inset-0 z-[2600] bg-black/90 flex items-center justify-center p-6">
           <div className="bg-white p-12 rounded-[50px] max-w-md w-full text-center border-8 border-red-500 animate-pulse">
              <AlertTriangle size={60} className="mx-auto text-red-500 mb-6" />
              <h3 className="text-4xl font-black italic uppercase mb-4 text-red-600">{tr(i18n.language, "ARE YOU SURE?", "ESTS SEGURO?")}</h3>
              <p className="text-zinc-950 font-bold text-sm uppercase tracking-widest mb-8 leading-relaxed">
                {tr(i18n.language, `You are throwing away $${projectedSavings.current.toFixed(2)}.`, `Ests botando $${projectedSavings.current.toFixed(2)}.`)}
              </p>
              <button onClick={handleJoin} className="w-full py-5 bg-zinc-950 text-white rounded-2xl font-black uppercase text-xs shadow-2xl mb-3">{tr(i18n.language, "Okay, I want the discount", "Ok, quiero el descuento")}</button>
              <button onClick={() => { setShowSecondChance(false); setCheckoutStep(4); }} className="w-full py-5 bg-zinc-100 text-zinc-400 rounded-2xl font-black uppercase text-xs">{tr(i18n.language, "Yes, charge full price", "S, cobrar todo")}</button>
           </div>
        </div>
      )}

      <nav className="sticky top-0 z-40 bg-white/70 backdrop-blur-3xl border-b h-24 flex items-center justify-between px-6 md:px-16 transition-all duration-300">
        <div className="flex items-center gap-6 cursor-pointer group" onClick={() => setView("home")}>
           <div className="w-14 h-14 rounded-[22px] bg-zinc-950 flex items-center justify-center shadow-2xl overflow-hidden group-hover:rotate-6 transition-transform">
             {config.customIcon ? <img src={config.customIcon} className="w-full h-full object-cover" /> : <ShoppingBag size={28} className="text-white" />}
           </div>
           <div>
             <h1 className="font-black text-3xl tracking-tighter uppercase italic leading-none">{config.brandName}</h1>
             <span className="text-[10px] font-black text-zinc-300 uppercase tracking-[0.4em]">{config.brandSubtitle || t('hero.subtitle')}</span>
           </div>
        </div>
        <div className="flex items-center gap-4 md:gap-8">
           <LanguageSwitcher />
           <button onClick={() => setView("track")} className="text-zinc-400 hover:text-black transition flex items-center gap-2 font-black text-[10px] uppercase tracking-widest hidden md:flex"><Clock size={18}/> {t('nav.orders')}</button>
           <button onClick={() => setView("cart")} className="relative p-3 md:p-5 bg-white rounded-[30px] border shadow-sm hover:shadow-2xl transition-all">
             <ShoppingBag size={24}/>
             {cart.length > 0 && <span className="absolute -top-1 -right-1 bg-emerald-500 text-white w-6 h-6 rounded-full text-[10px] flex items-center justify-center font-black border-4 border-white">{cart.reduce((a,b)=>a+b.qty,0)}</span>}
           </button>
           <button onClick={() => setView("admin")} className="p-3 text-zinc-200 hover:text-black transition-colors"><Lock size={20}/></button>
        </div>
      </nav>

      {view === 'home' && (
        <div className={`fixed right-6 z-30 md:left-auto md:right-10 md:w-96 animate-in slide-in-from-bottom-10 transition-all duration-500 ${cart.length > 0 ? 'bottom-48' : 'bottom-10'}`}>
             {isMember ? (
               <div onClick={() => setShowMemberMenu(!showMemberMenu)} className="bg-zinc-950 text-white p-6 rounded-[35px] shadow-2xl border-4 border-zinc-800 cursor-pointer relative overflow-hidden group hover:scale-105 transition-transform">
                  <div className="flex justify-between items-center mb-2">
                     <div className="flex items-center gap-3"><Crown size={20} className="text-yellow-400 fill-yellow-400" /><span className="font-black text-[10px] uppercase tracking-[0.3em] text-emerald-400">Elite Member</span></div>
                     {showMemberMenu ? <X size={20} className="text-zinc-500"/> : <Settings size={20} className="text-zinc-600 group-hover:text-white transition-colors"/>}
                  </div>
                  <p className="font-black text-xl uppercase mb-1 tracking-tighter">{form.name || "Bienvenido"}</p>
                  {showMemberMenu && (
                    <div className="absolute inset-0 bg-zinc-900/95 backdrop-blur-md flex flex-col items-center justify-center p-6 text-center animate-in fade-in">
                       <button onClick={(e) => { e.stopPropagation(); handleUnsubscribe(); }} className="w-full py-4 bg-red-500/10 text-red-500 border border-red-500/20 rounded-2xl font-black text-[10px] uppercase flex items-center justify-center gap-3 hover:bg-red-500 hover:text-white transition-all"><LogOut size={14}/> Cancel Membership</button>
                    </div>
                  )}
               </div>
             ) : (
                <div onClick={() => setShowJoinModal(true)} className="bg-white p-6 rounded-[35px] shadow-[0_20px_50px_rgba(0,0,0,0.1)] border-2 border-red-50 cursor-pointer relative overflow-hidden group hover:scale-105 transition-transform">
                  <div className="flex items-center gap-5">
                     <div className="w-14 h-14 bg-red-50 rounded-[20px] flex items-center justify-center text-red-500 animate-pulse shadow-inner"><AlertCircle size={28}/></div>
                     <div>
                        <p className="font-black text-[9px] uppercase text-red-400 tracking-[0.2em] mb-1">{tr(i18n.language, "ATTENTION", "ATENCIN")}</p>
                        <p className="font-black text-sm uppercase text-zinc-950 leading-tight">{tr(i18n.language, "You are losing money", "Ests perdiendo dinero")}</p>
                        <p className="font-bold text-[10px] uppercase text-emerald-500 mt-1">{tr(i18n.language, "Become a Member ->", "Hazte Miembro ->")}</p>
                     </div>
                  </div>
               </div>
             )}
        </div>
      )}

      <main className="pb-40">
        {view === 'home' && (
           <div className="animate-in fade-in duration-700">
              <header className="relative w-full h-screen overflow-hidden mb-0 group">
                 <img src={config.heroImage} className="absolute inset-0 w-full h-full object-cover brightness-75 transition duration-[20s] group-hover:scale-110" />
                 <div className="relative z-10 h-full flex flex-col items-center justify-center text-center p-12 bg-black/20 backdrop-blur-[2px]">
                    <h2 className="text-8xl md:text-[12rem] font-black italic uppercase tracking-tighter drop-shadow-2xl leading-none text-white mix-blend-overlay opacity-90">{config.brandName}</h2>
                    <div className="bg-white/10 backdrop-blur-xl px-12 py-5 rounded-full border border-white/20 mt-10 hover:bg-white/20 transition-all cursor-default">
                       <p className="text-[10px] md:text-xs font-black tracking-[0.6em] uppercase text-white">{config.brandSubtitle || t('hero.subtitle')}</p>
                    </div>
                 </div>
              </header>
              <div className="max-w-8xl mx-auto px-6 py-24">
                  <div className="flex gap-4 overflow-x-auto no-scrollbar pb-16 justify-center">
                     {["Todos", ...CATEGORY_OPTIONS].map(c => (
                       <button key={c} onClick={() => setActiveCategory(c)} className={`px-10 py-5 rounded-full font-black text-[10px] uppercase tracking-[0.3em] transition-all border-2 ${activeCategory===c ? 'bg-zinc-950 text-white border-zinc-950 shadow-2xl scale-110' : 'bg-white text-zinc-400 border-zinc-100 hover:border-zinc-300 hover:text-zinc-600'}`}>{t(`categories.${c === "Todos" ? "all" : c}`) || c}</button>
                     ))}
                  </div>
                  <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-10">
                     {products.filter(p => activeCategory === "Todos" || p.category === activeCategory).map(p => (
                       <ProductCard key={p.id} product={p} onAdd={addToCart} />
                     ))}
                  </div>
              </div>
           </div>
        )}

        {cart.length > 0 && view === 'home' && (
          <div onClick={() => setView('cart')} className="fixed bottom-0 left-0 right-0 z-50 bg-white border-t border-zinc-100 p-4 pb-8 shadow-[0_-10px_40px_rgba(0,0,0,0.1)] cursor-pointer hover:bg-zinc-50 transition-colors animate-in slide-in-from-bottom-full">
             <div className="max-w-4xl mx-auto flex justify-between items-center">
                <div className="flex flex-col">
                   <span className="text-[10px] font-black uppercase text-zinc-400 tracking-widest">Total</span>
                   <span className="text-3xl font-black italic text-zinc-950">${totals.total.toFixed(2)}</span>
                </div>
                <div className="flex flex-col items-end">
                   {isMember ? (
                      <>
                        <span className="text-[10px] font-black uppercase text-emerald-500 tracking-widest flex items-center gap-1"><Crown size={12}/> VIP SAVINGS</span>
                        <span className="text-xl font-black text-emerald-600">-${totals.discount.toFixed(2)}</span>
                      </>
                   ) : (
                      <div onClick={(e) => { e.stopPropagation(); setShowUpsellModal(true); }}>
                        <span className="text-[10px] font-black uppercase text-red-400 tracking-widest flex items-center gap-1 animate-pulse hover:text-red-600 cursor-pointer"><AlertCircle size={12}/> YOU COULD SAVE</span>
                        <span className="text-xl font-black text-red-500 hover:text-red-700 cursor-pointer">-${projectedSavings.current.toFixed(2)} (Click to Join)</span>
                      </div>
                   )}
                </div>
                <div className="bg-zinc-950 text-white w-12 h-12 rounded-full flex items-center justify-center shadow-lg">
                   <ChevronRight size={20}/>
                </div>
             </div>
          </div>
        )}

        {view === 'order_success' && lastOrder && (
           <div className="max-w-xl mx-auto py-20 p-6 text-center animate-in fade-in zoom-in">
              <div className="w-24 h-24 bg-emerald-500 rounded-full flex items-center justify-center mx-auto mb-8 shadow-2xl text-white animate-bounce"><Check size={48}/></div>
              <h2 className="text-5xl font-black italic uppercase tracking-tighter mb-4">Payment Success!</h2>
              <p className="text-zinc-400 font-bold uppercase text-xs tracking-[0.3em] mb-12">Order Ref: #{lastOrder.orderNumber}</p>
              
              <div className="bg-zinc-50 p-6 rounded-3xl mb-8 text-left space-y-4 shadow-inner">
                 <p className="text-[10px] font-black uppercase text-zinc-300 tracking-widest mb-2">Items Purchased</p>
                 {lastOrder.items.map((item:any, idx:number) => (
                    <div key={idx} className="flex items-center gap-4 bg-white p-3 rounded-2xl border border-zinc-100 shadow-sm">
                       <img src={item.image} className="w-12 h-12 rounded-xl object-cover" />
                       <div className="flex-1">
                          <p className="font-black text-xs uppercase">{item.name}</p>
                          <p className="text-[10px] text-zinc-400 font-bold uppercase">{item.size} / {item.color} / x{item.qty}</p>
                       </div>
                       <p className="font-black text-sm">${(item.price * item.qty).toFixed(2)}</p>
                    </div>
                 ))}
                 <div className="pt-4 border-t border-zinc-200 flex justify-between items-center">
                    <span className="font-black text-xs uppercase text-zinc-400">Total Paid</span>
                    <span className="font-black text-2xl text-emerald-600">${lastOrder.totals.total.toFixed(2)}</span>
                 </div>
              </div>

              <div className="bg-white p-8 rounded-[40px] shadow-xl border border-zinc-100 mb-8 space-y-4">
                 <p className="text-[10px] font-black uppercase text-zinc-300 tracking-widest">Send Receipt To Admin</p>
                 <div className="flex gap-4">
                    <a href={`https://wa.me/${config.adminPhone}?text=${encodeURIComponent(`New Order #${lastOrder.orderNumber} - Total: $${lastOrder.totals.total.toFixed(2)}`)}`} target="_blank" className="flex-1 py-4 bg-[#25D366] text-white rounded-2xl font-black text-xs uppercase flex items-center justify-center gap-2 hover:opacity-90"><MessageCircle size={18}/> WhatsApp</a>
                    <a href={`mailto:${config.zelleInfo}?subject=Order #${lastOrder.orderNumber}&body=Attached receipt for order #${lastOrder.orderNumber}`} className="flex-1 py-4 bg-zinc-950 text-white rounded-2xl font-black text-xs uppercase flex items-center justify-center gap-2 hover:opacity-90"><Mail size={18}/> Email</a>
                 </div>
              </div>

              <div className="flex gap-4">
                 <button onClick={() => setView('track')} className="flex-1 py-5 bg-white border-2 border-zinc-100 rounded-full font-black text-xs uppercase hover:bg-zinc-50">Track Order</button>
                 <button onClick={() => setView('home')} className="flex-1 py-5 bg-zinc-950 text-white rounded-full font-black text-xs uppercase hover:bg-zinc-800">Shop More</button>
              </div>
           </div>
        )}

        {view === 'cart' && (
           <div className="max-w-5xl mx-auto p-6 md:p-20">
              <button onClick={() => { setView("home"); setCheckoutStep(1); }} className="flex items-center gap-3 font-black text-xs uppercase text-zinc-400 mb-12 hover:text-zinc-950 transition-colors"><ArrowLeft size={18}/> {tr(i18n.language, "Back to Boutique", "Volver a la Boutique")}</button>
              
              <div className="grid lg:grid-cols-12 gap-16">
                 {/* CART ITEMS */}
                 <div className="lg:col-span-7 space-y-8">
                    <h2 className="text-7xl font-black italic uppercase tracking-tighter mb-10 text-zinc-950">{tr(i18n.language, "Your Bag", "Tu Bolsa")}</h2>
                    {cart.map(item => (
                      <div key={item.key} className="flex gap-8 items-stretch bg-white p-6 rounded-[40px] shadow-xl border border-zinc-50 group hover:border-zinc-200 transition-all">
                         <div className="w-32 h-40 rounded-[25px] overflow-hidden bg-zinc-50 shrink-0"><img src={item.image} className="w-full h-full object-cover mix-blend-multiply" /></div>
                         <div className="flex-1 flex flex-col justify-center">
                            <p className="font-black uppercase text-xl leading-none mb-2 tracking-tight">{item.name}</p>
                            <div className="flex gap-2 mb-4">
                               <span className="bg-zinc-100 px-3 py-1 rounded-lg text-[9px] font-black uppercase text-zinc-500">{item.size}</span>
                               <span className="bg-zinc-100 px-3 py-1 rounded-lg text-[9px] font-black uppercase text-zinc-500">{item.color}</span>
                               <span className="bg-zinc-100 px-3 py-1 rounded-lg text-[9px] font-black uppercase text-zinc-500">Qty: {item.qty}</span>
                            </div>
                            <p className="font-black text-3xl text-emerald-600 italic tracking-tighter">${(item.price * item.qty).toFixed(2)}</p>
                         </div>
                         <button onClick={() => removeFromCart(item.key)} className="self-start p-2 text-zinc-200 hover:text-red-500 transition-colors"><XCircle size={28}/></button>
                      </div>
                    ))}
                    {cart.length === 0 && <div className="py-32 text-center border-4 border-dashed border-zinc-100 rounded-[50px]"><p className="text-zinc-300 font-black text-3xl uppercase italic">Empty Bag</p></div>}
                 </div>

                 {/* CHECKOUT FLOW CONTAINER */}
                 <div className="lg:col-span-5 h-fit sticky top-32">
                    <div className="bg-zinc-950 p-12 rounded-[60px] shadow-2xl relative overflow-hidden text-white border border-zinc-800">
                        <div className="absolute top-0 right-0 w-64 h-64 bg-zinc-800 rounded-full blur-[100px] opacity-20 pointer-events-none"/>
                        
                        <h3 className="font-black uppercase text-2xl mb-10 italic tracking-wider relative z-10">
                           {checkoutStep === 1 ? tr(i18n.language, "Details", "Datos") : checkoutStep === 2 ? tr(i18n.language, "Payment Method", "Mtodo Pago") : tr(i18n.language, "Complete", "Finalizar")}
                        </h3>

                        {/* STEP 1: INFO */}
                        {checkoutStep === 1 && (
                           <div className="space-y-5 relative z-10 animate-in slide-in-from-right-10">
                              <input placeholder="FULL NAME" value={form.name} onChange={e => setForm({...form, name: e.target.value})} className="w-full p-5 bg-white/10 border border-white/10 rounded-[25px] font-bold text-xs uppercase text-white placeholder:text-zinc-600 focus:bg-white/20 transition-all outline-none" />
                              <input placeholder="NMERO DE TELFONO" value={form.phone} onChange={e => setForm({...form, phone: e.target.value})} className="w-full p-5 bg-white/10 border border-white/10 rounded-[25px] font-bold text-xs uppercase text-white placeholder:text-zinc-600 focus:bg-white/20 transition-all outline-none" />
                              <textarea placeholder="SHIPPING ADDRESS" value={form.address} onChange={e => setForm({...form, address: e.target.value})} className="w-full p-5 bg-white/10 border border-white/10 rounded-[25px] font-bold text-xs uppercase text-white placeholder:text-zinc-600 focus:bg-white/20 transition-all outline-none" rows={3} />
                              <button onClick={() => { if(form.name && form.phone) setCheckoutStep(2); else setToast({message:"Fill all fields", type:"error"}); }} className="w-full py-7 bg-white text-zinc-950 rounded-full font-black uppercase text-xs tracking-[0.3em] hover:scale-105 transition-transform shadow-xl mt-4">
                                 {tr(i18n.language, "Next Step", "Siguiente Paso")}
                              </button>
                           </div>
                        )}

                        {/* STEP 2: METHOD SELECTION */}
                        {checkoutStep === 2 && (
                           <div className="space-y-4 relative z-10 animate-in slide-in-from-right-10">
                              {[
                                 {id: 'card', label: 'Credit Card (Stripe)', icon: <CreditCard size={20}/>},
                                 {id: 'apple_pay', label: 'Apple / Google Pay', icon: <Smartphone size={20}/>},
                                 {id: 'zelle', label: 'Zelle Transfer', icon: <Wallet size={20}/>}
                              ].map(m => (
                                 <button key={m.id} onClick={() => handleMethodSelect(m.id)} className="w-full p-6 bg-white/5 rounded-[30px] flex items-center justify-between hover:bg-white/10 border border-white/5 hover:border-white/20 transition-all group">
                                    <div className="flex items-center gap-4">
                                       <div className="w-12 h-12 bg-white/10 rounded-2xl flex items-center justify-center text-white shadow-inner group-hover:bg-white group-hover:text-zinc-950 transition-colors">{m.icon}</div>
                                       <span className="font-black text-[10px] uppercase tracking-widest">{m.label}</span>
                                    </div>
                                    <ChevronRight size={16} className="text-zinc-600 group-hover:text-white"/>
                                 </button>
                              ))}
                           </div>
                        )}

                        {/* STEP 4: FINAL PAYMENT */}
                        {checkoutStep === 4 && (
                           <div className="relative z-10 animate-in slide-in-from-right-10">
                              <div className="space-y-4 mb-10 text-xs font-bold uppercase text-zinc-500 bg-white/5 p-8 rounded-[35px] border border-white/5">
                                 <div className="flex justify-between text-zinc-400"><span>Subtotal</span><span>${totals.subtotal.toFixed(2)}</span></div>
                                 <div className="flex justify-between text-emerald-400"><span>VIP Discount</span><span>-${totals.discount.toFixed(2)}</span></div>
                                 <div className="flex justify-between text-zinc-400"><span>Tax</span><span>${totals.tax.toFixed(2)}</span></div>
                                 <div className="flex justify-between text-3xl text-white pt-6 border-t border-white/10 italic font-black tracking-tighter"><span>Total</span><span>${totals.total.toFixed(2)}</span></div>
                              </div>

                              {selectedMethod === 'card' ? (
                                 stripePromise ? (
                                    <Elements stripe={stripePromise}>
                                       <StripeCardForm amount={totals.total.toFixed(2)} onSuccess={finalizeOrder} onError={(msg:string) => setToast({message: msg, type:"error"})} />
                                    </Elements>
                                 ) : (
                                    <p className="text-center text-red-400 font-bold uppercase text-xs">Stripe Key not configured in Admin!</p>
                                 )
                              ) : selectedMethod === 'zelle' ? (
                                 <>
                                    <div className="bg-emerald-500/20 p-8 rounded-[40px] border border-emerald-500/30 text-center mb-10 space-y-6">
                                       <div>
                                          <p className="text-[9px] font-black uppercase text-emerald-200 mb-2 tracking-widest">Zelle Number / Email:</p>
                                          <p className="text-xl font-black text-emerald-400 select-all bg-emerald-900/30 p-4 rounded-2xl border border-emerald-500/20">{config.zelleInfo || "N/A"}</p>
                                       </div>
                                       <div>
                                          <p className="text-[9px] font-black uppercase text-emerald-200 mb-2 tracking-widest">Instructions:</p>
                                          <p className="text-xs font-bold text-emerald-100 bg-emerald-900/30 p-4 rounded-2xl border border-emerald-500/20 leading-relaxed">{config.zelleInstructions}</p>
                                       </div>
                                    </div>
                                    <button onClick={() => finalizeOrder({id: 'zelle-manual'})} className="w-full py-7 bg-emerald-500 text-white rounded-full font-black uppercase text-xs tracking-[0.3em] hover:bg-emerald-400 transition-all shadow-[0_0_40px_rgba(16,185,129,0.3)]">
                                       Confirm Zelle Payment
                                    </button>
                                 </>
                              ) : (
                                 <button onClick={() => finalizeOrder({id: 'manual'})} className="w-full py-7 bg-white text-zinc-950 rounded-full font-black uppercase text-xs tracking-[0.3em] hover:scale-105 transition-transform">
                                    Complete Order
                                 </button>
                              )}
                           </div>
                        )}
                    </div>
                 </div>
              </div>
           </div>
        )}

        {view === 'admin' && <AdminPanel config={config} setConfig={setConfig} products={products} members={members} showAlert={showAlert} user={user} />}
        {view === 'track' && <TrackOrdersView setView={setView} user={user} config={config}/>}
      </main>
    </div>
  );
}

function ProductCard({ product, onAdd }: any) {
  const { t, i18n } = useTranslation();
  const [show, setShow] = useState(false);
  const [size, setSize] = useState("");
  const [color, setColor] = useState("");
  const [qty, setQty] = useState(1);
  const stock = product.stock !== undefined ? parseInt(product.stock) : 99;
  const isOut = stock <= 0;
  const sizes = (product.sizes?.length > 0) ? product.sizes : (SIZES_MAP[product.category] || SIZES_MAP["Default"]);
  const colors = (product.colors?.length > 0) ? COLOR_OPTIONS.filter(c => product.colors.includes(c.name)) : COLOR_OPTIONS;

  return (
    <div className="group flex flex-col h-full text-zinc-950 animate-in fade-in zoom-in">
       <div onClick={() => setShow(true)} className="relative aspect-[3/4.4] overflow-hidden rounded-[50px] bg-white shadow-lg border border-zinc-100 cursor-pointer group-hover:shadow-2xl transition-all">
        <img src={product.image} className="w-full h-full object-cover transition duration-1000 group-hover:scale-110" alt={product.name} />
        {isOut && <div className="absolute top-4 right-4 bg-red-500 text-white px-3 py-1 rounded-full text-[9px] font-black uppercase tracking-widest shadow-lg">{tr(i18n.language, "Pre-Order", "Pre-Orden")}</div>}
      </div>
      <div className="mt-5 px-3" onClick={() => setShow(true)}>
        <p className="text-[9px] font-black uppercase text-zinc-300 tracking-[0.3em] mb-1">{t(`categories.${product.category}`) || product.category}</p>
        <h3 className="font-bold text-zinc-950 truncate text-base uppercase mb-1">{product.name}</h3>
        <p className="font-black text-emerald-600 text-xl italic tracking-tighter">${Number(product.price).toFixed(2)}</p>
      </div>
      {show && (
        <div className="fixed inset-0 z-[1000] bg-zinc-950/95 backdrop-blur-3xl flex items-center justify-center p-0 md:p-8 animate-in fade-in zoom-in-95">
          <div className="bg-white rounded-none md:rounded-[60px] max-w-5xl w-full min-h-screen md:min-h-[600px] shadow-2xl relative overflow-hidden flex flex-col md:flex-row">
            <button onClick={() => setShow(false)} className="absolute top-6 right-8 text-zinc-300 hover:text-zinc-950 z-50"><X size={35} /></button>
            <div className="md:w-1/2 bg-zinc-50 flex items-center justify-center p-12"><img src={product.image} className="w-full h-full object-contain mix-blend-multiply" /></div>
            <div className="md:w-1/2 p-10 flex flex-col justify-between bg-white">
              <div>
                <h3 className="text-4xl font-black italic uppercase tracking-tighter mb-4">{product.name}</h3>
                <p className="text-zinc-400 text-xs uppercase font-bold mb-8 leading-relaxed tracking-widest">{product.description}</p>
                {isOut && <div className="mb-6 p-4 bg-yellow-50 border border-yellow-200 rounded-2xl flex gap-3"><AlertTriangle className="text-yellow-600" size={20} /><p className="text-[10px] text-yellow-700 font-bold uppercase">Out of Stock - Pre-order Available</p></div>}
                <div className="space-y-6">
                  <div><label className="text-[10px] font-black uppercase text-zinc-300 tracking-[0.3em] mb-3 block">{t('product.availableColors')}</label><div className="flex gap-3">{colors.map((c: any) => (<button key={c.name} onClick={() => setColor(c.name)} className={`w-10 h-10 rounded-full border-4 transition-all ${color === c.name ? 'border-zinc-950 scale-110' : 'border-transparent shadow-sm'}`} style={{ background: c.hex }} />))}</div></div>
                  <div><label className="text-[10px] font-black uppercase text-zinc-300 tracking-[0.3em] block mb-3">{t('product.selectSize')}</label><div className="flex flex-wrap gap-2">{sizes.map((s: any) => (<button key={s} onClick={() => setSize(s)} className={`min-w-[60px] h-12 rounded-xl border-2 font-black text-[10px] transition-all ${size === s ? "bg-zinc-950 text-white border-zinc-950" : "bg-white text-zinc-300 border-zinc-100 hover:border-zinc-300"}`}>{s}</button>))}</div></div>
                </div>
              </div>
              <div className="mt-10 flex gap-4 items-center">
                 <div className="flex items-center gap-4 bg-zinc-50 rounded-full px-4 py-2 border"><button onClick={() => setQty(Math.max(1, qty-1))}><Minus size={16}/></button><span className="font-black text-lg">{qty}</span><button onClick={() => setQty(qty+1)}><Plus size={16}/></button></div>
                 <button onClick={() => { if(size && color) { onAdd(product, size, color, qty, isOut); setShow(false); }}} disabled={!size || !color} className={`flex-1 py-6 rounded-full font-black uppercase text-xs tracking-widest shadow-xl text-white ${(!size||!color) ? 'bg-zinc-200' : isOut ? 'bg-yellow-500' : 'bg-zinc-950 hover:bg-emerald-600'}`}>{isOut ? "PRE-ORDER" : t('product.addToBag')}</button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

function AdminPanel({ config, setConfig, products, members, showAlert, user }: any) {
  const { i18n } = useTranslation();
  const [tab, setTab] = useState("orders");
  const [isLogged, setIsLogged] = useState(false);
  const [pass, setPass] = useState("");
  const [userLogin, setUserLogin] = useState(""); 
  
  const [vaultUnlocked, setVaultUnlocked] = useState(false);
  const [vaultPin, setVaultPin] = useState("");

  const [productData, setProductData] = useState<any>({ category: "Camisas", stock: 10 });
  const [editingId, setEditingId] = useState<string | null>(null);
  const [tempImage, setTempImage] = useState("");
  const [adminSelColors, setAdminSelColors] = useState<string[]>([]);
  const [adminSelSizes, setAdminSelSizes] = useState<string[]>([]);
  const [orders, setOrders] = useState<any[]>([]);
  const [memberSearch, setMemberSearch] = useState("");
  const [recoveryPinInput, setRecoveryPinInput] = useState("");
  const [showRecovery, setShowRecovery] = useState(false);
  const fileRef = useRef<HTMLInputElement>(null);
  const heroInputRef = useRef<HTMLInputElement>(null);
  const logoInputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if(!isLogged) return;
    const unsub = onSnapshot(collection(db, 'artifacts', appId, 'public', 'data', 'orders'), s => setOrders(s.docs.map(d => ({id: d.id, ...d.data()})).sort((a:any,b:any) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime())));
    return () => unsub();
  }, [isLogged]);

  useEffect(() => {
    if (editingId && productData) { setAdminSelColors(productData.colors || []); setAdminSelSizes(productData.sizes || []); } 
    else { setAdminSelColors([]); setAdminSelSizes([]); }
  }, [editingId, productData]);

  const handleLogin = () => { 
    if (userLogin === config.adminUsername && pass === config.adminPassword) { setIsLogged(true); } 
    else { showAlert("Credenciales Invlidas", "error"); }
  };
  
  const handleRecovery = () => { if(recoveryPinInput === config.recoveryPin) { setIsLogged(true); setShowRecovery(false); } };
  const toggleAdminColor = (c: string) => setAdminSelColors(p => p.includes(c) ? p.filter(x=>x!==c) : [...p, c]);
  const toggleAdminSize = (s: string) => setAdminSelSizes(p => p.includes(s) ? p.filter(x=>x!==s) : [...p, s]);

  const saveProduct = async (e: any) => {
    e.preventDefault();
    const data = { ...productData, price: parseFloat(productData.price), stock: parseInt(productData.stock), image: tempImage || productData.image, colors: adminSelColors, sizes: adminSelSizes, createdAt: new Date().toISOString() };
    if (editingId) { await updateDoc(doc(db, 'artifacts', appId, 'public', 'data', 'products', editingId), data); setEditingId(null); }
    else await addDoc(collection(db, 'artifacts', appId, 'public', 'data', 'products'), data);
    showAlert("Saved");
  };

  const saveConfig = async (e: any) => {
    e.preventDefault();
    const fd = new FormData(e.target);
    const nc: any = {...config};
    fd.forEach((v,k) => nc[k] = ['taxPercent','membershipCost','membershipDiscount', 'savingsPeriod', 'visitsPerMonth'].includes(k) ? parseFloat(v as string) : v);
    await setDoc(doc(db, 'artifacts', appId, 'public', 'data', 'settings', 'general'), nc);
    setConfig((prev:any) => ({...prev, ...nc})); showAlert("Settings Saved");
  };

  if (!isLogged) return (
    <div className="min-h-screen flex items-center justify-center bg-[#fafafa] p-6 animate-in fade-in duration-700">
      <div className="bg-white p-12 rounded-[50px] shadow-2xl max-w-md w-full text-center border border-zinc-100 relative">
         <div className="w-20 h-20 bg-zinc-950 rounded-[25px] flex items-center justify-center mx-auto mb-8 shadow-xl text-white -rotate-6"><Lock size={32}/></div>
         <h2 className="text-3xl font-black italic uppercase mb-10 tracking-tight">Admin Center</h2>
         {!showRecovery ? (
           <div className="space-y-5">
             <input placeholder="USUARIO" className="w-full p-5 bg-zinc-50 rounded-2xl font-bold text-xs text-center outline-none focus:ring-2 focus:ring-emerald-500/20" value={userLogin} onChange={e => setUserLogin(e.target.value)} />
             <input type="password" placeholder="CLAVE" className="w-full p-5 bg-zinc-50 rounded-2xl font-bold text-xs text-center outline-none focus:ring-2 focus:ring-emerald-500/20" value={pass} onChange={e => setPass(e.target.value)} />
             <button onClick={handleLogin} className="w-full py-6 bg-emerald-500 text-white rounded-full font-black uppercase text-xs tracking-widest shadow-xl hover:bg-emerald-600 transition-transform active:scale-95">CONFIRMAR</button>
             <button onClick={() => setShowRecovery(true)} className="text-[10px] font-black text-zinc-300 uppercase mt-6 hover:text-zinc-500">Olvid mi clave</button>
           </div>
         ) : (
           <div className="space-y-5">
             <p className="text-xs font-bold text-zinc-400 mb-4 uppercase">Ingrese PIN de Recuperacin</p>
             <input type="password" placeholder="0000" maxLength={4} className="w-full p-5 bg-zinc-50 rounded-2xl font-black text-xl text-center tracking-[1em] outline-none" value={recoveryPinInput} onChange={e => setRecoveryPinInput(e.target.value)} />
             <button onClick={handleRecovery} className="w-full py-6 bg-zinc-950 text-white rounded-full font-black uppercase text-xs tracking-widest shadow-xl">VALIDAR</button>
             <button onClick={() => setShowRecovery(false)} className="text-[10px] font-black text-zinc-300 uppercase mt-6">Cancelar</button>
           </div>
         )}
      </div>
    </div>
  );

  return (
    <div className="p-4 md:p-10 animate-in fade-in">
      <div className="flex gap-4 mb-10 justify-center">
        {['orders', 'products', 'members', 'config'].map(t => <button key={t} onClick={() => setTab(t)} className={`px-6 py-3 rounded-full font-black uppercase text-[10px] tracking-widest ${tab===t?'bg-zinc-950 text-white':'bg-white text-zinc-400'}`}>{t}</button>)}
        <button onClick={() => setIsLogged(false)} className="px-6 py-3 rounded-full bg-red-50 text-red-500 font-black uppercase text-[10px]">Exit</button>
      </div>

      {tab === 'config' && (
        <form onSubmit={saveConfig} className="max-w-4xl mx-auto bg-white p-12 rounded-[50px] shadow-xl space-y-12 border">
           <div className="flex items-center gap-4 border-b pb-8">
              <div className="w-16 h-16 bg-zinc-950 rounded-2xl flex items-center justify-center text-white"><Settings size={30}/></div>
              <div><h3 className="text-3xl font-black uppercase italic">Master Control</h3><p className="text-zinc-400 font-bold text-xs uppercase tracking-widest">System Configuration</p></div>
           </div>
           <div className="grid md:grid-cols-2 gap-10">
             <div className="space-y-6">
                <h4 className="font-black text-xs uppercase text-zinc-400 border-b pb-2 flex items-center gap-2"><ImageIcon size={14}/> Brand Identity</h4>
                <div className="space-y-3"><label className="text-[10px] font-black ml-3 text-zinc-400">BRAND NAME</label><input name="brandName" defaultValue={config.brandName} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" /></div>
                <div className="space-y-3"><label className="text-[10px] font-black ml-3 text-zinc-400">SUBTITLE</label><input name="brandSubtitle" defaultValue={config.brandSubtitle} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" /></div>
                <div className="space-y-3">
                   <label className="text-[10px] font-black ml-3 text-zinc-400">HERO IMAGE (Large)</label>
                   <div className="flex gap-2">
                      <input name="heroImage" defaultValue={config.heroImage} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" placeholder="https://..." />
                      <button type="button" onClick={() => heroInputRef.current?.click()} className="p-4 bg-zinc-100 rounded-2xl hover:bg-zinc-200"><Camera size={18}/></button>
                      <input ref={heroInputRef} type="file" hidden onChange={e => handleImageUploadUtility(e, (url:string) => setConfig({...config, heroImage: url}), showAlert)} />
                   </div>
                </div>
                <div className="space-y-3">
                   <label className="text-[10px] font-black ml-3 text-zinc-400">MINI LOGO (Small)</label>
                   <div className="flex gap-2">
                      <input name="customIcon" defaultValue={config.customIcon} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" placeholder="https://..." />
                      <button type="button" onClick={() => logoInputRef.current?.click()} className="p-4 bg-zinc-100 rounded-2xl hover:bg-zinc-200"><Camera size={18}/></button>
                      <input ref={logoInputRef} type="file" hidden onChange={e => handleImageUploadUtility(e, (url:string) => setConfig({...config, customIcon: url}), showAlert)} />
                   </div>
                </div>
             </div>
             <div className="space-y-6">
                <h4 className="font-black text-xs uppercase text-zinc-400 border-b pb-2 flex items-center gap-2"><FileText size={14}/> Financial Rules</h4>
                <div className="grid grid-cols-2 gap-4">
                   <div className="space-y-3"><label className="text-[10px] font-black ml-3 text-zinc-400">TAX %</label><input name="taxPercent" type="number" defaultValue={config.taxPercent} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" /></div>
                   <div className="space-y-3"><label className="text-[10px] font-black ml-3 text-zinc-400">VIP DISCOUNT %</label><input name="membershipDiscount" type="number" defaultValue={config.membershipDiscount} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" /></div>
                </div>
                <div className="space-y-3"><label className="text-[10px] font-black ml-3 text-zinc-400">MEMBERSHIP COST ($)</label><input name="membershipCost" type="number" defaultValue={config.membershipCost} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" /></div>
                <div className="grid grid-cols-2 gap-4">
                   <div className="space-y-3"><label className="text-[10px] font-black ml-3 text-zinc-400">DURATION (MONTHS)</label><input name="savingsPeriod" type="number" defaultValue={config.savingsPeriod} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" /></div>
                   <div className="space-y-3"><label className="text-[10px] font-black ml-3 text-zinc-400">REQ. VISITS/MO</label><input name="visitsPerMonth" type="number" defaultValue={config.visitsPerMonth} className="w-full p-4 bg-zinc-50 rounded-2xl font-bold text-xs" /></div>
                </div>
             </div>
           </div>
           <div className="p-8 bg-zinc-50 rounded-[40px] border border-zinc-200 space-y-6">
              <h4 className="font-black text-xs uppercase text-zinc-500 flex items-center gap-2"><Key size={16}/> Payment Gateway Vault</h4>
              {!vaultUnlocked ? (
                 <div className="flex flex-col items-center justify-center py-10 bg-white rounded-3xl border border-zinc-200 shadow-sm">
                    <div className="w-12 h-12 bg-zinc-100 rounded-full flex items-center justify-center mb-4 text-zinc-400"><Lock size={20}/></div>
                    <p className="text-[10px] font-black uppercase text-zinc-400 tracking-widest mb-4">Vault Locked</p>
                    <div className="flex gap-2">
                       <input type="password" placeholder="Enter Admin Password" className="p-3 bg-zinc-50 rounded-xl text-xs font-bold outline-none border focus:border-zinc-300" value={vaultPin} onChange={e => setVaultPin(e.target.value)} />
                       <button type="button" onClick={() => { if(vaultPin === config.adminPassword || vaultPin === config.adminPin || vaultPin === "0000") setVaultUnlocked(true); else showAlert("Access Denied", "error"); }} className="p-3 bg-zinc-950 text-white rounded-xl"><Unlock size={16}/></button>
                    </div>
                 </div>
              ) : (
                 <div className="grid gap-4 animate-in fade-in slide-in-from-top-4">
                    <div className="space-y-2"><label className="text-[9px] font-black ml-3 text-zinc-400">STRIPE PUBLISHABLE KEY (PK_...)</label><input name="stripePublicKey" type="password" defaultValue={config.stripePublicKey} className="w-full p-4 bg-white rounded-2xl font-mono text-[10px] border tracking-widest" placeholder="pk_test_..." /></div>
                    <div className="space-y-2"><label className="text-[9px] font-black ml-3 text-zinc-400">STRIPE SECRET KEY (SK_...)</label><input name="stripeSecretKey" type="password" defaultValue={config.stripeSecretKey} className="w-full p-4 bg-white rounded-2xl font-mono text-[10px] border tracking-widest" placeholder="sk_test_..." /></div>
                    <button type="button" onClick={() => setVaultUnlocked(false)} className="text-[10px] font-black uppercase text-zinc-400 hover:text-red-500 mt-2">Lock Vault</button>
                 </div>
              )}
              <div className="grid md:grid-cols-2 gap-6 pt-4 border-t border-zinc-200">
                 <div className="space-y-2"><label className="text-[9px] font-black ml-3 text-zinc-400">ZELLE NUMBER / EMAIL</label><input name="zelleInfo" defaultValue={config.zelleInfo} className="w-full p-4 bg-white rounded-2xl font-bold text-xs border" placeholder="e.g. 555-0199" /></div>
                 <div className="space-y-2"><label className="text-[9px] font-black ml-3 text-zinc-400">ZELLE INSTRUCTIONS</label><textarea name="zelleInstructions" defaultValue={config.zelleInstructions} className="w-full p-4 bg-white rounded-2xl font-bold text-xs border h-[52px]" placeholder="Instructions for customer..." /></div>
                 <div className="space-y-2"><label className="text-[9px] font-black ml-3 text-zinc-400">ADMIN PHONE (FOR WHATSAPP)</label><input name="adminPhone" defaultValue={config.adminPhone} className="w-full p-4 bg-white rounded-2xl font-bold text-xs border" placeholder="e.g. 15551234567" /></div>
              </div>
           </div>
           <div className="p-8 bg-red-50 rounded-[40px] border border-red-100 space-y-6">
             <h4 className="font-black text-xs uppercase text-red-400 flex items-center gap-2"><Shield size={16}/> Security Zone</h4>
             <div className="grid md:grid-cols-3 gap-6">
               <div className="space-y-2"><label className="text-[9px] font-black ml-3 text-red-300">ADMIN USER</label><input name="adminUsername" defaultValue={config.adminUsername} className="w-full p-4 bg-white rounded-2xl font-bold text-xs border border-red-100" /></div>
               <div className="space-y-2"><label className="text-[9px] font-black ml-3 text-red-300">ADMIN PASSWORD</label><input name="adminPassword" type="password" defaultValue={config.adminPassword} className="w-full p-4 bg-white rounded-2xl font-bold text-xs border border-red-100" /></div>
               <div className="space-y-2"><label className="text-[9px] font-black ml-3 text-red-300">RECOVERY PIN</label><input name="recoveryPin" type="password" maxLength={4} defaultValue={config.recoveryPin} className="w-full p-4 bg-white rounded-2xl font-bold text-xs border border-red-100 tracking-[0.5em] text-center" /></div>
             </div>
           </div>
           <button className="w-full py-6 bg-zinc-950 text-white rounded-2xl font-black uppercase text-xs tracking-widest shadow-xl hover:scale-105 transition-transform">Save Master Configuration</button>
        </form>
      )}

      {tab === 'products' && (
        <div className="grid md:grid-cols-2 gap-10">
           <form onSubmit={saveProduct} className="bg-white p-10 rounded-[50px] shadow-xl space-y-6 h-fit border">
              <h3 className="text-2xl font-black uppercase italic mb-2">Item Manager</h3>
              <select value={productData.category} onChange={e => setProductData({...productData, category: e.target.value})} className="w-full p-4 bg-zinc-50 rounded-2xl text-xs font-bold uppercase outline-none">{CATEGORY_OPTIONS.map(c => <option key={c} value={c}>{c}</option>)}</select>
              <input placeholder="Name" value={productData.name || ""} onChange={e => setProductData({...productData, name: e.target.value})} className="w-full p-4 bg-zinc-50 rounded-2xl text-xs font-bold" />
              <div className="grid grid-cols-2 gap-4">
                <input type="number" placeholder="Price $" value={productData.price || ""} onChange={e => setProductData({...productData, price: e.target.value})} className="w-full p-4 bg-zinc-50 rounded-2xl text-xs font-bold" />
                <input type="number" placeholder="Stock Qty" value={productData.stock} onChange={e => setProductData({...productData, stock: e.target.value})} className="w-full p-4 bg-zinc-50 rounded-2xl text-xs font-bold" />
              </div>
              <textarea placeholder="Description (Auto-filled)" value={productData.description || ""} onChange={e => setProductData({...productData, description: e.target.value})} className="w-full p-4 bg-zinc-50 rounded-2xl text-xs font-bold" rows={4}/>
              <div className="p-4 bg-zinc-50 rounded-3xl border">
                 <label className="text-[9px] font-black text-zinc-400 block mb-2">COLORS</label>
                 <div className="flex flex-wrap gap-2">{COLOR_OPTIONS.map(c => <button key={c.name} type="button" onClick={()=>toggleAdminColor(c.name)} className={`w-8 h-8 rounded-full border ${adminSelColors.includes(c.name)?'ring-2 ring-black':''}`} style={{background:c.hex}}/>)}</div>
              </div>
              <div className="p-4 bg-zinc-50 rounded-3xl border">
                 <label className="text-[9px] font-black text-zinc-400 block mb-2">SIZES ({productData.category})</label>
                 <div className="flex flex-wrap gap-2">{(SIZES_MAP[productData.category]||SIZES_MAP.Default).map((s:string) => <button key={s} type="button" onClick={()=>toggleAdminSize(s)} className={`px-3 py-1 rounded-lg text-[9px] font-bold border ${adminSelSizes.includes(s)?'bg-black text-white':'bg-white'}`}>{s}</button>)}</div>
              </div>
              <div className="flex flex-col items-center justify-center p-6 bg-zinc-50 rounded-[30px] border border-dashed border-zinc-300">
                 <div onClick={() => fileRef.current?.click()} className="flex flex-col items-center gap-2 cursor-pointer hover:opacity-70">
                    <Camera size={24}/>
                    <span className="text-[9px] font-black uppercase">Upload Image</span>
                    <input ref={fileRef} type="file" className="hidden" onChange={e => handleImageUploadUtility(e, setTempImage, showAlert)} />
                 </div>
                 {(tempImage || productData.image) && <img src={tempImage || productData.image} className="mt-4 w-20 h-20 object-cover rounded-xl shadow-md" />}
              </div>
              <button className="w-full py-5 bg-zinc-950 text-white rounded-2xl font-black uppercase text-xs tracking-widest shadow-xl">{editingId ? "Update Product" : "Create Product"}</button>
           </form>
           <div className="grid grid-cols-2 gap-6 content-start">
              {products.map((p: any) => (
                <div key={p.id} className="bg-white p-5 rounded-[30px] shadow-md border flex flex-col justify-between hover:scale-105 transition-transform">
                   <div className="flex justify-between items-start mb-3">
                      <span className="text-[9px] font-black uppercase bg-zinc-100 px-3 py-1 rounded-full text-zinc-400">{p.category}</span>
                      <span className={`text-[9px] font-black px-2 py-1 rounded-full ${p.stock>0?'bg-emerald-100 text-emerald-600':'bg-red-100 text-red-500'}`}>Qty: {p.stock}</span>
                   </div>
                   <img src={p.image} className="w-full aspect-square object-cover rounded-2xl mb-3 bg-zinc-50" />
                   <p className="font-bold text-xs uppercase leading-tight mb-4">{p.name}</p>
                   <div className="flex gap-2 mt-auto">
                     <button onClick={() => { setProductData(p); setEditingId(p.id); setTempImage(""); }} className="flex-1 py-3 bg-zinc-50 hover:bg-zinc-100 rounded-xl"><Edit2 size={14} className="mx-auto"/></button>
                     <button onClick={() => deleteDoc(doc(db, 'artifacts', appId, 'public', 'data', 'products', p.id))} className="flex-1 py-3 bg-red-50 hover:bg-red-100 text-red-500 rounded-xl"><Trash2 size={14} className="mx-auto"/></button>
                   </div>
                </div>
              ))}
           </div>
        </div>
      )}

      {tab === 'orders' && (
        <div className="space-y-6">
           {orders.map(o => (
             <div key={o.id} className="bg-white p-8 rounded-[40px] shadow-lg border flex flex-col md:flex-row gap-8">
                <div className="flex-1">
                   <div className="flex gap-4 items-center mb-6">
                      <span className="bg-zinc-950 text-white px-6 py-2 rounded-full text-[10px] font-black tracking-widest">#{o.orderNumber}</span>
                      <span className="text-xs text-zinc-400 font-bold">{new Date(o.createdAt).toLocaleDateString()}</span>
                   </div>
                   <div className="grid md:grid-cols-2 gap-8">
                      <div>
                         <p className="text-[9px] font-black text-zinc-400 uppercase mb-2">Customer</p>
                         <p className="font-bold uppercase text-lg">{o.customer.name}</p>
                         <p className="text-xs text-zinc-500">{o.customer.phone}</p>
                         <p className="text-xs text-zinc-400 italic mt-1">{o.customer.address}</p>
                      </div>
                      <div>
                         <p className="text-[9px] font-black text-zinc-400 uppercase mb-2">Order Total</p>
                         <p className="font-black text-3xl text-emerald-600 italic">${o.totals?.total.toFixed(2)}</p>
                         <p className="text-[9px] font-bold text-zinc-400 uppercase mt-2">Method: {o.method}</p>
                      </div>
                   </div>
                   {/* ADMIN TICKET WITH SMALL IMAGES */}
                   <div className="mt-6 bg-zinc-50 p-4 rounded-3xl space-y-3">
                      {o.items.map((i:any, idx:number) => (
                        <div key={idx} className="flex items-center gap-4 border-b border-zinc-100 pb-2 last:border-0 last:pb-0">
                           <img src={i.image} className="w-10 h-10 rounded-lg object-cover bg-white border" />
                           <div className="flex-1 flex justify-between text-[10px] font-bold uppercase">
                              <span>{i.qty}x {i.name} ({i.size})</span>
                              <span>${(i.price*i.qty).toFixed(2)}</span>
                           </div>
                        </div>
                      ))}
                   </div>
                   <div className="mt-6"><ChatSystem orderId={o.id} isAdmin={true} user={user} /></div>
                </div>
                <div className="md:w-48 flex flex-col gap-2">
                   {Object.keys(STATUS_STEPS).map(s => (
                      <button key={s} onClick={() => updateDoc(doc(db, 'artifacts', appId, 'public', 'data', 'orders', o.id), {status: s})} className={`py-3 rounded-xl text-[9px] font-black uppercase ${o.status===s ? 'bg-emerald-500 text-white shadow-lg' : 'bg-zinc-50 text-zinc-400'}`}>{s}</button>
                   ))}
                   <button onClick={() => deleteDoc(doc(db, 'artifacts', appId, 'public', 'data', 'orders', o.id))} className="mt-4 py-3 text-red-400 text-[9px] font-black uppercase hover:text-red-600">Delete Record</button>
                </div>
             </div>
           ))}
        </div>
      )}

      {tab === 'members' && (
        <div className="bg-white p-10 rounded-[50px] shadow-xl border">
           <div className="flex justify-between items-center mb-8">
              <h3 className="text-2xl font-black italic uppercase">VIP List</h3>
              <div className="relative"><Search className="absolute left-4 top-1/2 -translate-y-1/2 text-zinc-300" size={16}/><input value={memberSearch} onChange={e=>setMemberSearch(e.target.value)} placeholder="Search phone..." className="pl-12 pr-4 py-3 bg-zinc-50 rounded-full text-xs font-bold outline-none"/></div>
           </div>
           <div className="grid gap-4">
              {members.filter((m:any) => m.phone.includes(memberSearch)).map((m:any) => (
                 <div key={m.id} className="flex justify-between items-center p-4 bg-zinc-50 rounded-2xl hover:bg-emerald-50 transition-colors">
                    <div className="flex items-center gap-4">
                       <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center shadow-sm text-emerald-500"><Crown size={18}/></div>
                       <div><p className="font-black text-xs uppercase">{m.name}</p><p className="text-[10px] text-zinc-400">{m.phone}</p></div>
                    </div>
                    <button onClick={() => deleteDoc(doc(db, 'artifacts', appId, 'public', 'data', 'members', m.id))} className="p-2 text-red-300 hover:text-red-500"><Trash2 size={16}/></button>
                 </div>
              ))}
           </div>
        </div>
      )}
    </div>
  );
}

// --- TRACK ORDERS VIEW (DEFINIDO AFUERA TAMBIN) ---
function TrackOrdersView({ setView, user, config }: any) {
  const { t } = useTranslation();
  const [orders, setOrders] = useState<any[]>([]);
  const [selected, setSelected] = useState<any>(null);

  useEffect(() => {
    const saved = JSON.parse(localStorage.getItem(`orders_${appId}`) || "[]");
    if(saved.length > 0 && db) {
       onSnapshot(collection(db, 'artifacts', appId, 'public', 'data', 'orders'), s => {
          setOrders(s.docs.map(d=>({id:d.id, ...d.data()})).filter((o:any) => saved.includes(o.orderNumber)));
       });
    }
  }, []);

  if(selected) return <OrderReceipt order={selected} config={config} onBack={()=>setSelected(null)} user={user}/>;

  return (
    <div className="max-w-4xl mx-auto py-12 p-6 animate-in fade-in">
       <button onClick={() => setView("home")} className="flex items-center gap-2 font-black text-xs uppercase text-zinc-400 mb-8"><ArrowLeft size={16}/> Back</button>
       <h2 className="text-5xl font-black italic uppercase tracking-tighter mb-12">My Orders</h2>
       <div className="grid gap-6">
          {orders.map(o => (
             <div key={o.id} onClick={() => setSelected(o)} className="bg-white p-8 rounded-[40px] border shadow-lg cursor-pointer hover:scale-105 transition-transform flex justify-between items-center">
                <div>
                   <p className="font-black text-xl uppercase">#{o.orderNumber}</p>
                   <p className="text-xs text-zinc-400 font-bold">{new Date(o.createdAt).toLocaleDateString()}</p>
                </div>
                <div className="bg-emerald-50 text-emerald-600 px-6 py-2 rounded-full font-black text-[10px] uppercase">{o.status}</div>
             </div>
          ))}
          {orders.length === 0 && <p className="text-center text-zinc-300 font-bold uppercase italic py-20">No orders found.</p>}
       </div>
    </div>
  );
}